/* Estilos para el contenedor de la lista de productos */
.product-list-container {
  padding: 20px;
  font-family: Arial, sans-serif;
}

/* Título de la lista de productos */
.product-list-title {
  font-size: 2rem;
  color: #333;
  margin-bottom: 20px;
}

/* Estilos para cada producto en la lista */
.product-list {
  list-style-type: none;
  padding: 0;
}

.product-item {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  padding: 15px;
  margin: 10px 0;
  border-radius: 8px;
  transition: transform 0.2s, box-shadow 0.2s;
}

.product-item:hover {
  transform: scale(1.02);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Estilo para los títulos de cada producto */
.product-title {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 5px;
}

/* Estilo para el ID del producto */
.product-id {
  color: #666;
  margin-bottom: 15px;
}

/* Botón para asignar plantilla */
.assign-template-btn {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.assign-template-btn:hover {
  background-color: #0056b3;
}

/* Estilos para el modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  width: 80%;
  max-width: 600px;
  position: relative;
}

/* Botón de cierre del modal */
.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: transparent;
  border: none;
  font-size: 1.5rem;
  color: #333;
  cursor: pointer;
}

.close-btn:hover {
  color: red;
}

/* Título del modal */
.modal-title {
  font-size: 1.75rem;
  color: #333;
  margin-bottom: 20px;
}

/* Estilos para el texto de carga */
.loading-text,
.error-text {
  font-size: 1.2rem;
  text-align: center;
  color: #f00;
}

.loading-text {
  font-style: italic;
}

.error-text {
  color: #cc0000;
}
  