.saved-products-container {
  width: 100%;
  min-height: 60vh;
  padding: 2vh 5vw; 
  background: #f5f5f5;
}

.page-title {
  font-size: 3vh;
  margin-bottom: 2vh;
  text-align: center;
  color: #333;
}

.product-item-save-products {
  background: #fff;
  border-radius: 1vh;
  padding: 2vh;
  margin-bottom: 2vh;
  box-shadow: 0 0.5vh 1vh rgba(0, 0, 0, 0.1);
}

.product-title {
  font-size: 2.5vh;
  margin-bottom: 1vh;
  color: #222;
}

/* ID del producto */
.product-id {
  font-size: 2vh;
  color: #666;
  margin-bottom: 2vh;
}

/* Título de la sección de plantillas */
.templates-title {
  font-size: 2vh;
  margin-bottom: 1vh;
  color: #333;
}

/* Lista de plantillas: con alto máximo para scroll */
.template-list-asign {
  max-width: 100%;
  list-style: none;
  padding: 0;
  margin: 0 0 2vh 0;
  max-height: 20vh; /* Se establece un alto máximo relativo al viewport */
  overflow-y: auto;
  overflow-x: hidden;
}

/* Cada item de plantilla */
.template-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1vh;
  border-bottom: 0.1vh solid #ddd;
}

/* Botón para eliminar plantilla */
.delete-template-btn {
  padding: 1vh 2vw;
  font-size: 1.8vh;
  background: #dc3545;
  margin-right: 6vh;
  color: #fff;
  border: none;
  border-radius: 1vh;
  cursor: pointer;
  transition: background 0.3s;
}

.delete-template-btn:hover {
  background: #b02a37;
}

/* Contenedor para agregar una plantilla */
.add-template-container {
  display: flex;
  align-items: center;
  gap: 2vw;
  margin-top: 2vh;
}

/* Input para ingresar el ID de la plantilla */
.template-id-input {
  flex: 1;
  padding: 1vh;
  font-size: 1.8vh;
  border: 0.1vh solid #ccc;
  border-radius: 1vh;
}

/* Botón para agregar plantilla */
.add-template-btn {
  padding: 1vh 2vw;
  font-size: 1.8vh;
  background: #28a745;
  color: #fff;
  border: none;
  border-radius: 1vh;
  cursor: pointer;
  transition: background 0.3s;
}

.add-template-btn:hover {
  background: #218838;
}

/* Ajustes responsivos para pantallas más pequeñas */
@media (max-width: 768px) {
  .saved-products-container {
    padding: 2vh 3vw;
  }
  .page-title {
    font-size: 4vh;
  }
  .product-item-save-products {
    padding: 3vh;
  }
  .product-title {
    font-size: 3vh;
  }
  .product-id,
  .templates-title {
    font-size: 2.5vh;
  }
  .template-id-input,
  .delete-template-btn,
  .add-template-btn {
    font-size: 2vh;
  }
  .add-template-container {
    flex-direction: column;
    gap: 2vh;
  }
}
