/* Estilos para el modal */
.template-manager-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

/* Contenedor del modal */
.template-manager-content {
  background: white;
  min-width: 60vw;
  max-width: 60vw;
  max-height: 70vh;
  min-height: 70vh;
  padding: 3vh;
  border-radius: 1vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 0.5vh 2vh rgba(0, 0, 0, 0.2);
  overflow-y: auto;
}

/* Título principal */
.template-manager-title {
  font-size: 2.5vh;
  font-weight: bold;
  text-align: center;
  margin-bottom: 2vh;
}

/* Subtítulo */
.template-manager-subtitle {
  font-size: 2vh;
  margin-bottom: 1vh;
}

/* Lista de plantillas */
.template-manager-list {
  list-style: none;
  padding: 0;
  margin: 0;
  max-height: 40vh;
  overflow-y: auto;
}

/* Ítem de la lista */
.template-manager-item {
  display: flex;
  align-items: center;
  gap: 1vh;
  padding: 1vh;
  border-bottom: 0.2vh solid #ddd;
  font-size: 1.8vh;
}

/* Checkbox */
.template-manager-checkbox {
  transform: scale(1.2);
  cursor: pointer;
}

/* Botón de asignación */
.template-manager-btn {
  width: 100%;
  padding: 1.5vh;
  font-size: 2vh;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 1vh;
  cursor: pointer;
  transition: background 0.3s;
}

.template-manager-btn:hover {
  background: #0056b3;
}

/* Responsive */
@media (max-width: 600px) {
  .template-manager-content {
    width: 90vw;
    height: 75vh;
  }

  .template-manager-title {
    font-size: 2vh;
  }

  .template-manager-item {
    font-size: 1.6vh;
  }

  .template-manager-btn {
    font-size: 1.8vh;
  }
}
