/* ============================================
   Estilos para Dashboard - Toggle Components
   ============================================ */

/* Contenedor principal: ocupa el 100% de la altura del viewport */
.toggle-container {
  width: 100%;
  height: 100vh; /* Altura completa del viewport */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 2vh; /* Espaciado interno en función del viewport */
  box-sizing: border-box;
  background-color: #f5f5f5;
  font-family: Arial, sans-serif;
}

/* Contenedor de botones: centra los botones horizontalmente */
.button-container {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 2vh;
  flex-wrap: wrap;
}

/* Botones de toggle */
.toggle-button {
  padding: 1.5vh 3vh;
  margin: 1vh;
  font-size: 2vh;
  border: none;
  background-color: #ddd;
  border-radius: 0.8vh;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
  outline: none;
}

/* Botón activo */
.toggle-button.active {
  background-color: #007bff;
  color: #fff;
}

/* Área de despliegue de componentes */
.component-display {
  width: 100%;
  flex: 1; /* Ocupa el espacio restante */
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
}

/* Estilos para cada uno de los componentes internos */
.first-component,
.second-component,
.third-component {
  width: 100%;
  height: 80vh; /* Por ejemplo, cada componente ocupa el 80% de la altura del viewport */
  background-color: #fff;
  padding: 2vh;
  box-sizing: border-box;
  border-radius: 1vh;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}

/* ================================
   Media Queries para Responsividad
   ================================ */

/* Para pantallas medianas (tabletas) */
@media screen and (max-width: 768px) {
  .toggle-container {
    padding: 1.5vh;
  }
  
  .toggle-button {
    font-size: 1.8vh;
    padding: 1.2vh 2.5vh;
    margin: 1vh;
  }
  
  .first-component,
  .second-component,
  .third-component {
    height: 75vh;
    padding: 1.5vh;
    border-radius: 0.8vh;
  }
}

/* Para pantallas pequeñas (móviles) */
@media screen and (max-width: 480px) {
  .button-container {
    flex-direction: column;
    align-items: center;
  }
  
  .toggle-button {
    width: 90%;
    margin: 1vh 0;
    font-size: 1.6vh;
    padding: 1.2vh 2vh;
  }
  
  .first-component,
  .second-component,
  .third-component {
    height: 70vh;
    padding: 1vh;
    border-radius: 0.5vh;
  }
}
