/* Estilos para el botón de inicio de sesión */
.login-btn {
  background-color: #ffcc00;  /* Color principal de Mercado Libre */
  color: #333;               /* Texto oscuro para contraste */
  padding: 12px 24px;         /* Espaciado cómodo */
  font-size: 16px;            /* Tamaño adecuado de fuente */
  border: none;               /* Sin borde */
  border-radius: 8px;         /* Bordes redondeados */
  cursor: pointer;           /* Manito al pasar por encima */
  transition: background-color 0.3s ease, transform 0.2s ease;  /* Transiciones suaves */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);  /* Sombra suave */
  margin-top: 20px;           /* Espaciado superior */
  width: 100%;                /* Botón a todo el ancho del contenedor */
  max-width: 320px;           /* Limitar el ancho máximo */
  display: block;             /* Mostrar en bloque */
  text-align: center;         /* Centrado de texto */
}

/* Efecto hover: cambio de color y sombra */
.login-btn:hover {
  background-color: #ffb600;  /* Cambio de color al pasar el mouse */
  transform: translateY(-2px); /* Pequeña elevación */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);  /* Sombra más pronunciada */
}

/* Efecto de focus: cuando el botón tiene el foco */
.login-btn:focus {
  outline: none;             /* Sin borde de enfoque predeterminado */
  box-shadow: 0 0 0 3px rgba(255, 204, 0, 0.5); /* Resaltar con sombra */
}
  